.skills__bar {
  width: 0%;
}

.quali-text {
  font-size: 20px;
  top: 10px;
}

.qualifications-container {
  position: absolute;
  display: flex;
  justify-content: flex-start;
  align-items: center;
  height: 80%;
  width: 90%;
  overflow-x: scroll;
  overflow-y: hidden;
  margin-top: 15px;
  user-select: none;
}

.qualifications-container .qualification {
  position: relative;
  height: 550px;
  min-width: 460px;
  background-color: rgb(9, 6, 31);
  border-radius: 20px;
  margin: 0 20px 0 0;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: center;
}

.qualifications-container .qualification .content {
  height: 85%;
  width: 100%;
  border-radius: 20px 20px 0px 0px;
  margin-top: 0px;
  background-size: contain;
  background-position: center;
}

.qualifications-container .qualification h1 {
  font-size: 20px;
  text-transform: uppercase;
  margin-top: 10px;
}


.qualifications-container .qualification .content.illustrator {
  background: rgba(255, 255, 255, 0.103) url(../props/illustrator.png) no-repeat;
  background-position: center;
  background-size: 90%;
}
.qualifications-container .qualification .content.illustrator:hover ~ .skills__illustrator{
  transition: 1s;
  width: 85.5%;
}

.qualifications-container .qualification .content.photoshop {
  background: rgba(255, 255, 255, 0.103) url(../props/photoshop.png) no-repeat;
  background-position: center;
  background-size: cover;
  background-size: 90%;
}
.qualifications-container .qualification .content.photoshop:hover ~ .skills__photoshop{
  transition: 1s;
  width: 81%;
}

.qualifications-container .qualification .content.indesign {
  background: rgba(255, 255, 255, 0.103) url(../props/indesign.png) no-repeat;
  background-position: center;
  background-size: cover;
  background-size: 90%;
}
.qualifications-container .qualification .content.indesign:hover ~ .skills__indesign{
  transition: 1s;
  width: 72%;
}

.qualifications-container .qualification .content.bootstrap {
  background: rgba(255, 255, 255, 0.103) url(../props/bootstrap.png) no-repeat;
  background-position: center;
  background-size: cover;
  background-size: 90%;
}
.qualifications-container .qualification .content.react {
  background: rgba(255, 255, 255, 0.103) url(../props/react.png) no-repeat;
  background-position: center;
  background-size: cover;
  background-size: 90%;
}
.qualifications-container .qualification .content.c-plus-plus {
  background: rgba(255, 255, 255, 0.103) url(../props/c-plus-plus.png) no-repeat;
  background-position: center;
  background-size: cover;
  background-size: 90%;
}
.qualifications-container .qualification .content.c-sharp {
  background: rgba(255, 255, 255, 0.103) url(../props/c-sharp.png);
  background-position: center;
  background-size: cover;
  background-size: 66px;
}
.qualifications-container .qualification .content.kotlin {
  background: rgba(255, 255, 255, 0.103) url(../props/kotlin.png);
  background-position: center;
  background-size: cover;
  background-size: 66px;
}

@media (max-width: 820px) {
  .quali-text {
    font-size: 30px;
    top: 10px;
    text-align: center;
  }

  .qualifications-container {
    position: absolute;
    display: flex;
    justify-content: flex-start;
    align-items: center;
    height: 80%;
    width: 98%;
    overflow-x: scroll;
    overflow-y: hidden;
    margin-top: 15px;
  }

  .qualifications-container .qualification {
    position: relative;
    height: 80%;
    min-width: 95%;
    background-color: rgb(9, 6, 31);
    border-radius: 20px;
    margin: 0 30px 0 0;
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: center;
  }

  .qualifications-container .qualification .content {
    height: 85%;
    width: 90%;
    border-radius: 15px;
    margin-top: 20px;
    background-size: contain;
    background-position: center;
  }

  .qualifications-container .qualification h1 {
    font-size: 18px;
    text-transform: uppercase;
    margin-top: 10px;
    font-weight: 200;
  }
}

