*{
  margin: 0;
  padding: 0;
}
.text-box{
  font-size: 3vw;
  font-family: sans-serif;
  position: relative;
  text-align: center;

  z-index: 3;
  opacity: 0.5;
}

.test3{
  animation: mover 6s linear infinite;
  display: inline-block;
  color: #fff;
  font-weight: bolder;
}
@keyframes mover
{
  0%{
      text-shadow: 0 0 30px rgba(0, 0, 0, 0);
    }
  50%{
      text-shadow: 0 0 30px rgba(0, 0, 0, 0.30);
      transform: translateY(30px);
    }
  100%
    {
      text-shadow: 0 0 30px rgba(0, 0, 0, 0);
    }
}
.test3:nth-child(1)
{
  animation-delay: 0.5s;
}
.test3:nth-child(2)
{
  animation-delay: 1s;
}
.test3:nth-child(3)
{
  animation-delay: 1.5s;
}
.test3:nth-child(4)
{
  animation-delay: 2s;
}
.test3:nth-child(5)
{
  animation-delay: 2.5s;
}
.test3:nth-child(6)
{
  animation-delay: 3s;
}
.test3:nth-child(7)
{
  animation-delay: 3.5s;
}
.test3:nth-child(8)
{
  animation-delay: 4s;
}

.test1{
  z-index: 1;
}



.home-container {
  position: relative;
  min-height: 100vh;
  padding: 50px 40px;
  margin-top: 7px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.home-container .logo {
  position: inherit;
  margin-top: -50px;
  height: 520px;
  width: 520px;
  display: flex;
  justify-content: center;
  align-items: center;
  border-radius: 50%;
  user-select: none;
}

.home-container .logo .hover-show {
  position: absolute;
  height: 300px;
  width: 300px;
  border-radius: 50%;
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 5;
  cursor: pointer;
}

.home-container .logo .hover-show.active {
  animation: showspin 8s linear 1s infinite;
}

@keyframes showspin {
  to {
    transform: rotate(360deg);
  }
}

.home-container .logo .circle {
  position: absolute;
  height: 100px;
  width: 100px;
  border-radius: 50%;
  background-color: rgb(4, 4, 44);
  display: flex;
  justify-content: center;
  align-items: center;
  transition: 0.2s ease;
  opacity: 0;
}

.home-container .logo .hover-show.active .text-box {
  opacity: 0;
}

.home-container .logo .hover-show.active .circle {
  opacity: 1;
}

.home-container .logo .hover-show.active .circle:nth-child(1) {
  background: url(../props/html.png);
  background-size: cover;
  background-position: center;
  position: absolute;
  transform: translateY(-210%);
}
.home-container .logo .hover-show.active .circle:nth-child(2) {
  position: absolute;
  transform: translateX(150%) translateY(-150%);
  background: url(../props/css.png);
  background-size: cover;
  background-position: center;
}
.home-container .logo .hover-show.active .circle:nth-child(3) {
  position: absolute;
  transform: translateX(210%);
  background: url(../props/js.png);
  background-size: cover;
  background-position: center;
}
.home-container .logo .hover-show.active .circle:nth-child(4) {
  position: absolute;
  transform: translateX(150%) translateY(150%);
  background: url(../props/bootstrap.png);
  background-size: cover;
  background-position: center;
}
.home-container .logo .hover-show.active .circle:nth-child(5) {
  position: absolute;
  transform: translateY(210%);
  background: url(../props/react.png);
  background-size: cover;
  background-position: center;
}
.home-container .logo .hover-show.active .circle:nth-child(6) {
  position: absolute;
  transform: translateX(-150%) translateY(150%);
  background: url(../props/photoshop.png);
  background-size: cover;
  background-position: center;
}
.home-container .logo .hover-show.active .circle:nth-child(7) {
  position: absolute;
  transform: translateX(-210%);
  background: url(../props/illustrator.png);
  background-size: cover;
  background-position: center;
}
.home-container .logo .hover-show.active .circle:nth-child(8) {
  position: absolute;
  transform: translateX(-150%) translateY(-150%);
  background: url(../props/indesign.png);
  background-size: cover;
  background-position: center;
}

.home-container img {
  position: absolute;
  height: 300px;
  width: 300px;
  border-radius: 50%;
  border: 3px solid rgb(108, 161, 197);
  transform: translate(-200px);
  opacity: 0;
  animation: fade-in 1s ease 1 0.5s forwards;
}

@keyframes fade-in {
  to {
    transform: translate(0);
    opacity: 1;
  }
}

.home-container h2 {
  position: relative;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  max-width: 60%;
  margin-top: 50px;
}

.home-container h2 span {
  font-size: 25px;
  opacity: 0.8;
  padding-bottom: 5px;
  border-bottom: 2px solid var(--color-pri);
  font-family: "Open Sans Condensed", sans-serif;
}

.home-container h2 p {
  font-size: 20px;
  opacity: 1;
  font-weight: 100;
  margin-top: 1px;
  font-family: "Open Sans", sans-serif;
}

.scroll-down {
  transform: rotate(90deg);
  position: absolute;
  right: 0;
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 10px;
  cursor: pointer;
}

.scroll-down hr {
  background-color: blue;
  width: 20px;
}

.scroll-down h5 {
  font-size: 18px;
  text-transform: capitalize;
  font-weight: 100;
}

.scroll-down .scroll {
  transform: rotate(-90deg);
  font-size: 20px;
}

