.scroll-down {
  transform: rotate(90deg);
  position: absolute;
  top:380px;
  right: -80px;
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 10px;
  cursor: pointer;
}

.scroll-down hr {
  background-color: blue;
  width: 20px;
}

.scroll-down h5 {
  font-size: 18px;
  text-transform: capitalize;
  font-weight: 100;
}

.scroll-down .scroll {
  transform: rotate(-90deg);
  font-size: 20px;
}

.skills__bar {
  position: absolute;
  left: 10px;
  bottom: 70px;
  background-color: #f9d342;
  height: .25rem;
  border-radius: .5rem;
  z-index: -10;
}

.test{
  display: flex;
}

.top{
  position: relative;
  top:5px;
  left:-5px;
}

.skills__icon {
  font-size: 2rem;
  margin-right: var(--mb-2);
  color: var(--first-color);
}

