.skills__bar {
  width: 0%;
}

.quali-text {
  font-size: 20px;
  top: 10px;
}

.qualifications-container {
  position: absolute;
  display: flex;
  justify-content: flex-start;
  align-items: center;
  height: 80%;
  width: 90%;
  overflow-x: scroll;
  overflow-y: hidden;
  margin-top: 15px;
  user-select: none;
}

.qualifications-container .qualification {
  position: relative;
  height: 550px;
  min-width: 460px;
  background-color: rgb(9, 6, 31);
  border-radius: 20px;
  margin: 0 20px 0 0;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: center;
  z-index: 1;
}

.qualifications-container .qualification .content {
  height: 85%;
  width: 100%;
  border-radius: 20px;
  margin-top: 0px;
  background-size: contain;
  background-position: center;
}

.qualifications-container .qualification h1 {
  font-size: 20px;
  text-transform: uppercase;
  margin-top: 10px;
}

.qualifications-container .qualification .content.html {
  background: rgba(255, 255, 255, 0.103) url(../props/html.png) no-repeat;
  background-position: center;
  background-size: 90%;
}
.qualifications-container .qualification .content.html:hover ~ .skills__html{
  transition: 1s;
  width: 85.5%;
}

.qualifications-container .qualification .content.css {
  background: rgba(255, 255, 255, 0.103) url(../props/css.png) no-repeat;
  background-position: center;
  background-size: cover;
  background-size: 90%;
}
.qualifications-container .qualification .content.css:hover ~ .skills__css{
  transition: 1s;
  width: 81%;
}

.qualifications-container .qualification .content.javascript {
  background: rgba(255, 255, 255, 0.103) url(../props/js.png) no-repeat;
  background-position: center;
  background-size: cover;
  background-size: 90%;
}
.qualifications-container .qualification .content.javascript:hover ~ .skills__javascript{
  transition: 1s;
  width: 72%;
}

.qualifications-container .qualification .content.bootstrap {
  background: rgba(255, 255, 255, 0.103) url(../props/bootstrap.png) no-repeat;
  background-position: center;
  background-size: cover;
  background-size: 90%;
}
.qualifications-container .qualification .content.bootstrap:hover ~ .skills__bootstrap{
  transition: 1s;
  width: 72%;
}

.qualifications-container .qualification .content.react {
  background: rgba(255, 255, 255, 0.103) url(../props/react.png) no-repeat;
  background-position: center;
  background-size: cover;
  background-size: 90%;
}
.qualifications-container .qualification .content.react:hover ~ .skills__react{
  transition: 1s;
  width: 67.5%;
}

.qualifications-container .qualification .content.c-plus-plus {
  background: rgba(255, 255, 255, 0.103) url(../props/c-plus-plus.png) no-repeat;
  background-position: center;
  background-size: cover;
  background-size: 90%;
}
.qualifications-container .qualification .content.c-sharp {
  background: rgba(255, 255, 255, 0.103) url(../props/c-sharp.png);
  background-position: center;
  background-size: cover;
  background-size: 66px;
}
.qualifications-container .qualification .content.kotlin {
  background: rgba(255, 255, 255, 0.103) url(../props/kotlin.png);
  background-position: center;
  background-size: cover;
  background-size: 66px;
}

